.filter_pop__content{
    padding: 12px 27px;
    display: flex;   
    flex-wrap: wrap;
}

.ss__filterDisable{
    pointer-events: none;
}

.d__filter-btnbox{
    // flex: 1 0 50%;
    display: inline;
}

.filter_pop{
    width: 300px;

    .scrape__filterActionBtns{
        display: flex;
        flex-grow: 1;
        font-family: "LatoWeb";
        margin-bottom: 15px;
    }

    .scrape__filterTagBtns{
        display: flex;
        flex-wrap: wrap;
        font-family: "LatoWebLight";

        .d__filter-btn{
            flex-grow: 1;
            background-color: #46316c;
            color: white;
            padding: 0 15px;
            font-size: 15px;
            border: none;
            outline: none;
            border-radius: 20px;
            margin: 5px 4px;
            &:hover{
                background-color: #9d6ae0;
            }
        }
    }

    
    .d__filter-selall{
        flex-grow: 1;
        display: flex;
        cursor: pointer;
        align-items: center;

        input[type=checkbox]{
            height: 15px;
            width: 15px;    
        }
        span{
            margin-left: 10px;
        }
    }

    .scrape__mobileType{
        padding: 6px 8px !important;
        border-radius: 5px;
        box-shadow: none;
        // appearance: none;
        outline: none;
    }
}

.active-filter{
    background-color: #9d6ae0!important;
}